/* ==================================================================
 *
 * Card Component: 
 * A card has a title, some content, and some tags
 *
 *
===================================================================== */

/* ==================================================================
## Config
===================================================================== */

$card-base-font-size:		1rem;
/*$card-body-font-size:		1em;*/

/* ==================================================================
## Layout
===================================================================== */

.card {

	/*border-top: 1px solid #e1e1e1;*/

	&__title 	{	margin: $base-row-pad 0;	}
	&__slide 	{	/**/	}
	&__body p 	{	margin: $base-row-pad 0;	}
	&__tags 	{	margin: $base-row-pad 0;	}
}



/* ==================================================================
## Typography
===================================================================== */

.card {

	/* 	
		module-level styling
		typically only font-size is specified at module level
		and em sizing below it is relative to it
	*/
	font-size:				$card-base-font-size;

	&__title 	{	

		/*font-size: 		$card-title-font-size;*/
		/*line-height: 		$card-title-line-height;*/
		/*color: 			$card-title-font-color;*/
	}

	&__slide 	{	/**/	}

	&__body p 	{

		/*font-size: 			$card-body-font-size;*/
		/*line-height: 		$card-body-line-height;*/
		/*color: 			$card-body-font-color;*/
		text-align:			justify;
	}

	&__tags 	{	/**/	}
}

/* ==================================================================
## Temp Subclasses
===================================================================== */

